import { Component, OnDestroy, OnInit } from '@angular/core';
import { ElasticSearchResult } from '@models4insight/atlas/api';
import { untilDestroyed } from '@models4insight/utils';
import { ModelExplorerService } from 'libs/modelview2/src/lib/model-explorer.service';
import { ModelElement } from 'libs/modelview2/src/lib/parsers';
import { combineLatest, Observable } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';
import { SearchResultService } from '../../../../services/search-result/search-result.service';

@Component({
  selector: 'models4insight-model-explorer-data-governance',
  templateUrl: 'model-explorer-data-governance.component.html',
  styleUrls: ['model-explorer-data-governance.component.scss'],
  providers: [SearchResultService]
})
export class ModelExplorerDataGovernanceComponent implements OnInit, OnDestroy {
  isLoading$: Observable<boolean>;
  searchResult$: Observable<ElasticSearchResult>;
  showDetailsCard$: Observable<boolean>;

  constructor(
    private readonly modelExplorerService: ModelExplorerService,
    private readonly searchResultService: SearchResultService
  ) {}

  ngOnInit() {
    this.isLoading$ = this.searchResultService.select(
      'isRetrievingSearchResult'
    );
    this.searchResult$ = this.searchResultService.select('searchResult');

    this.showDetailsCard$ = combineLatest([
      this.modelExplorerService.select('selectedEntity'),
      this.searchResultService.select('searchResult')
    ]).pipe(
      map(
        ([selectionId, searchResult]) => selectionId === searchResult.guid?.raw
      )
    );

    this.modelExplorerService
      .select('selectedEntity')
      .pipe(
        switchMap(selectionId =>
          this.modelExplorerService.select(['elements', selectionId], {
            includeFalsy: true
          })
        ),
        filter(Boolean),
        untilDestroyed(this)
      )
      .subscribe((selectedElement: ModelElement) => {
        this.searchResultService.guid = selectedElement.id;
      });
  }

  ngOnDestroy() {}
}
