import { Component, Inject, InjectionToken, OnInit, Optional } from '@angular/core';
import { EntityAttributes, Facet, FilterValuesByFieldName, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { defaultIfFalsy } from '@models4insight/utils';
import { Dictionary } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';

export const PROCESS_SYSTEMS_FILTER_FACTORY = new InjectionToken<
  typeof createSystemsCardsFilter
>('ProcessSystemsFilterFactory');

export const defaultFilters: FilterValuesByFieldName = {
  supertypenames: ['m4i_system']
};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  typename: { type: 'value', size: 100 },
  derivedsystem: { type: 'value', size: 100 }
};

const sortingOptions: string[] = ['name'];

function createSystemsCardsFilter(relationshipAttributes: EntityAttributes) {
  const systems = relationshipAttributes.system ?? [];
  return { ...defaultFilters, guid: systems.map(system => system.guid) };
}

@Component({
  selector: 'models4insight-systems-cards',
  templateUrl: 'systems-cards.component.html',
  styleUrls: ['systems-cards.component.scss'],
  providers: [{ provide: 'searchServiceFacets', useValue: facets }]
})
export class SystemsCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  constructor(
    private readonly entityDetailsService: EntityDetailsService,
    @Optional()
    @Inject(PROCESS_SYSTEMS_FILTER_FACTORY)
    private readonly systemsFilterFactory: typeof createSystemsCardsFilter
  ) {
    if (!this.systemsFilterFactory) {
      this.systemsFilterFactory = createSystemsCardsFilter;
    }
  }

  ngOnInit() {
    this.defaultFilters$ = this.entityDetailsService
      .select(['entityDetails', 'entity', 'relationshipAttributes'], {
        includeFalsy: true
      })
      .pipe(
        defaultIfFalsy([]),
        map(systems => this.systemsFilterFactory(systems))
      );
  }
}
