import { Component, OnInit } from '@angular/core';
import { AtlasEntityDef, Facet, FilterValuesByFieldName, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { defaultIfFalsy } from '@models4insight/utils';
import { Dictionary } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';

const defaultFilters: FilterValuesByFieldName = {
  supertypenames: ['m4i_dataset']
};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  typename: { type: 'value', size: 100 },
  derivedsystem: { type: 'value', size: 100 },
  derivedcollection: { type: 'value', size: 100 }
};

const sortingOptions: string[] = ['name'];

function createOutputsCardsFilter(outputs: AtlasEntityDef[]) {
  return { ...defaultFilters, guid: outputs.map(output => output.guid) };
}

@Component({
  selector: 'models4insight-outputs-cards',
  templateUrl: 'outputs-cards.component.html',
  styleUrls: ['outputs-cards.component.scss'],
  providers: [{ provide: 'searchServiceFacets', useValue: facets }]
})
export class OutputsCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  constructor(readonly entityDetailsService: EntityDetailsService) {}

  ngOnInit() {
    this.defaultFilters$ = this.entityDetailsService
      .select(
        ['entityDetails', 'entity', 'relationshipAttributes', 'outputs'],
        {
          includeFalsy: true
        }
      )
      .pipe(defaultIfFalsy([]), map(createOutputsCardsFilter));
  }
}
