import { Component, OnInit } from '@angular/core';
import { Facet, FilterValuesByFieldName, getDataQuality, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { Dictionary } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';

const defaultFilters: FilterValuesByFieldName = {};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  dataqualityruledimension: { type: 'value', size: 100 }
};

const resultFields = {
  guid: { raw: {} },
  name: { raw: {} },
  dataqualityruledescription: { raw: {} },
  dataqualityruledimension: { raw: {} },
  dqscore: {
    raw: {}
  }
};

const sortingOptions: string[] = [
  'name',
  'dataqualityruledimension',
  'dqscore'
];

function createFieldsCardsFilter(fieldId: string) {
  return { ...defaultFilters, fieldguid: [fieldId] };
}

@Component({
  selector: 'models4insight-data-quality-cards',
  templateUrl: 'data-quality-cards.component.html',
  styleUrls: ['data-quality-cards.component.scss'],
  providers: [
    { provide: 'searchServiceFacets', useValue: facets },
    { provide: 'searchServiceQueryResolver', useValue: getDataQuality },
    { provide: 'searchServiceResultFields', useValue: resultFields }
  ]
})
export class DataQualityCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  constructor(readonly entityDetailsService: EntityDetailsService) {}

  ngOnInit() {
    this.defaultFilters$ = this.entityDetailsService
      .select(['entityDetails', 'entity', 'guid'])
      .pipe(map(createFieldsCardsFilter));
  }
}
