import { Component, OnInit } from '@angular/core';
import { Facet, FilterValuesByFieldName, getGovernanceQuality, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { Dictionary } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';

const defaultFilters: FilterValuesByFieldName = {};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  guid: { type: 'value', size: 100 }
};

const resultFields = {
  compliant: { raw: {} },
  dataqualityruledescription: {
    raw: {}
  },
  dataqualityruledimension: {
    raw: {}
  },
  guid: { raw: {} },
  name: { raw: {} },
  result: { raw: {} }
};

const sortingOptions: string[] = [
  'name',
  'dataqualityruledimension',
  'compliant'
];

function createRulesCardsFilter(entityId: string) {
  return { ...defaultFilters, guid: [entityId] };
}

@Component({
  selector: 'models4insight-governance-quality-cards',
  templateUrl: 'governance-quality-cards.component.html',
  styleUrls: ['governance-quality-cards.component.scss'],
  providers: [
    { provide: 'searchServiceFacets', useValue: facets },
    { provide: 'searchServiceQueryResolver', useValue: getGovernanceQuality },
    { provide: 'searchServiceResultFields', useValue: resultFields }
  ]
})
export class GovernanceQualityCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  constructor(readonly entityDetailsService: EntityDetailsService) {}

  ngOnInit() {
    this.defaultFilters$ = this.entityDetailsService
      .select(['entityDetails', 'entity', 'guid'])
      .pipe(map(createRulesCardsFilter));
  }
}
