import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../services/entity-details/entity-details.service';
import { FilteredPropertiesService } from '../../services/filtered-properties/filtered-properties.service';
import { SearchResultService } from '../../services/search-result/search-result.service';

@Component({
  selector: 'models4insight-domain-details',
  templateUrl: 'domain-details.component.html',
  styleUrls: ['domain-details.component.scss']
})
export class DomainDetailsComponent implements OnInit {
  dataQualityCount$: Observable<number>;
  dataQualityScore$: Observable<number>;
  entityCount$: Observable<number>;
  propertyCount$: Observable<number>;

  constructor(
    private readonly entityDetailsService: EntityDetailsService,
    private readonly filteredPropertiesService: FilteredPropertiesService,
    private readonly searchResultService: SearchResultService
  ) {}

  ngOnInit() {
    this.dataQualityScore$ = this.searchResultService.select([
      'searchResult',
      'dqscore_overall',
      'raw'
    ]);

    this.dataQualityCount$ = this.searchResultService.select(
      ['searchResult', 'dqscorecnt_overall', 'raw'],
      { includeFalsy: true }
    );

    this.entityCount$ = this.entityDetailsService.select([
      'entityDetails',
      'entity',
      'relationshipAttributes',
      'dataEntity',
      'length'
    ]);

    this.propertyCount$ = this.filteredPropertiesService.state.pipe(
      map(properties => Object.keys(properties).length)
    );
  }
}
