import { Component } from '@angular/core';
import { EntityAttributes } from '@models4insight/atlas/api';
import { defaultFilters, SYSTEM_PROCESSES_FILTER_FACTORY } from '../system/processes-cards/processes-cards.component';

function createProcessesCardsFilter(relationshipAttributes: EntityAttributes) {
  const { system = [], microservice = [] } = relationshipAttributes;
  return {
    ...defaultFilters,
    guid: [...system, ...microservice].map(entity => entity.guid)
  };
}

@Component({
  selector: 'models4insight-kubernetes-pod-details',
  templateUrl: 'kubernetes-pod-details.component.html',
  styleUrls: ['kubernetes-pod-details.component.scss'],
  providers: [
    {
      provide: SYSTEM_PROCESSES_FILTER_FACTORY,
      useValue: createProcessesCardsFilter
    }
  ]
})
export class KubernetesPodDetailsComponent {}
