import { Component, OnInit, ViewChild } from '@angular/core';
import { Facet, FilterValuesByFieldName, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { Dictionary } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';
import { SearchService } from '../../../services/search/search.service';
import { DetailsCardsListComponent } from '../../components/details-cards-list/details-cards-list.component';

const defaultFilters: FilterValuesByFieldName = {};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  typename: { type: 'value', size: 100 },
  deriveddatadomain: { type: 'value', size: 100 },
  deriveddataentity: { type: 'value', size: 100 },
  name: { type: 'value', size: 100 }
};

const sortingOptions: string[] = [
  'typename',
  'deriveddatadomain',
  'deriveddataentity',
  'name',
  'dqscore_accuracy',
  'dqscore_completeness',
  'dqscore_timeliness',
  'dqscore_uniqueness',
  'dqscore_validity'
];

function createFieldsCardsFilter(personName: string) {
  return { ...defaultFilters, derivedperson: [personName] };
}

@Component({
  selector: 'models4insight-governance-roles-cards',
  templateUrl: 'governance-roles-cards.component.html',
  styleUrls: ['governance-roles-cards.component.scss'],
  providers: [{ provide: 'searchServiceFacets', useValue: facets }]
})
export class GovernanceRolesCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  @ViewChild(DetailsCardsListComponent, { read: SearchService, static: true })
  readonly searchService: SearchService;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  constructor(private readonly entityDetailsService: EntityDetailsService) {}

  ngOnInit() {
    this.defaultFilters$ = this.entityDetailsService
      .select(['entityDetails', 'entity', 'attributes', 'name'])
      .pipe(map(createFieldsCardsFilter));
  }
}
