import { Component, OnInit } from '@angular/core';
import { Facet, FilterValuesByFieldName, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { Dictionary } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';

const defaultFilters: FilterValuesByFieldName = {
  supertypenames: ['m4i_field']
};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  derivedsystem: { type: 'value', size: 100 },
  derivedcollection: { type: 'value', size: 100 },
  deriveddataset: { type: 'value', size: 100 }
};

const sortingOptions: string[] = [
  'name',
  'derivedsystem',
  'derivedcollection',
  'deriveddataset',
  'dqscore_accuracy',
  'dqscore_completeness',
  'dqscore_timeliness',
  'dqscore_uniqueness',
  'dqscore_validity'
];

function createFieldsCardsFilter(attributeGuid: string) {
  return { ...defaultFilters, deriveddataattributeguid: [attributeGuid] };
}

@Component({
  selector: 'models4insight-fields-cards',
  templateUrl: 'fields-cards.component.html',
  styleUrls: ['fields-cards.component.scss'],
  providers: [{ provide: 'searchServiceFacets', useValue: facets }]
})
export class FieldsCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  constructor(readonly entityDetailsService: EntityDetailsService) {}

  ngOnInit() {
    this.defaultFilters$ = this.entityDetailsService
      .select('entityId')
      .pipe(map(createFieldsCardsFilter));
  }
}
