import { Component, OnInit, ViewChild } from '@angular/core';
import { Facet, FilterValuesByFieldName, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { Dictionary } from 'lodash';
import { combineLatest, Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';
import { DetailsCardsListComponent } from '../../components/details-cards-list/details-cards-list.component';

const defaultFilters: FilterValuesByFieldName = {
  supertypenames: ['m4i_collection']
};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  derivedperson: { type: 'value', size: 100 }
};

const sortingOptions: string[] = [
  'name',
  'dqscore_accuracy',
  'dqscore_completeness',
  'dqscore_timeliness',
  'dqscore_uniqueness',
  'dqscore_validity'
];

function createCollectionsCardsFilter(systemGuid: string) {
  return { ...defaultFilters, parentguid: [systemGuid] };
}

function createDerivedCollectionsCardsFilter(systemGuid: string) {
  return { ...defaultFilters, derivedsystemguid: [systemGuid] };
}

@Component({
  selector: 'models4insight-collections-cards',
  templateUrl: 'collections-cards.component.html',
  styleUrls: ['collections-cards.component.scss'],
  providers: [{ provide: 'searchServiceFacets', useValue: facets }]
})
export class CollectionsCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  @ViewChild(DetailsCardsListComponent, { static: true })
  private readonly detailsCardsList: DetailsCardsListComponent;

  constructor(readonly entityDetailsService: EntityDetailsService) {}

  ngOnInit() {
    this.defaultFilters$ = combineLatest([
      this.entityDetailsService.select('entityId'),
      this.detailsCardsList.showAllDescendants.pipe(startWith(false))
    ]).pipe(
      map(([id, showDescendants]) =>
        showDescendants
          ? createDerivedCollectionsCardsFilter(id)
          : createCollectionsCardsFilter(id)
      )
    );
  }
}
