import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { faArrowAltCircleDown, faFilter } from '@fortawesome/free-solid-svg-icons';
import { ElasticSearchResult, FiltersInOrder, FilterValuesByFieldName, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { defaultSimpleSearchInputContext, SimpleSearchInputContext } from '@models4insight/components';
import { untilDestroyed } from '@models4insight/utils';
import { isEqual } from 'lodash';
import { combineLatest, Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { v4 as uuid4 } from 'uuid';
import { FilterService } from '../../../services/filter/filter.service';
import { SearchService } from '../../../services/search/search.service';

const searchBarContext: SimpleSearchInputContext = {
  ...defaultSimpleSearchInputContext,
  label: null,
  placeholder: 'Search for an entity'
};

@Component({
  selector: 'models4insight-details-cards-list',
  templateUrl: 'details-cards-list.component.html',
  styleUrls: ['details-cards-list.component.scss'],
  providers: [SearchService, FilterService]
})
export class DetailsCardsListComponent implements OnInit, OnDestroy {
  @Output() showAllDescendants = new EventEmitter<boolean>();

  readonly arrowDown = faArrowAltCircleDown;
  readonly descendantsControl = new FormControl(false);
  readonly descendantsControlId = uuid4();
  readonly faFilter = faFilter;
  readonly searchBarContext = searchBarContext;

  entities$: Observable<ElasticSearchResult[]>;
  filter$: Observable<FiltersInOrder[]>;
  isFilterActive$: Observable<boolean>;
  isLoadingResults$: Observable<boolean>;
  query$: Observable<string>;

  @Input() enableDescendantsControl = true;
  @Input() enableDetailsPageLink = true;
  @Input() sortingOptions: string[] = [];
  @Input() title: string;

  private _defaultFilters: FilterValuesByFieldName = {};
  private _defaultSorting: SortingDirectionByFieldName = { _score: 'asc' };

  constructor(
    private readonly filterService: FilterService,
    private readonly searchService: SearchService
  ) {}

  ngOnInit() {
    this.entities$ = this.searchService
      .select(['searchResults', 'results'])
      .pipe(startWith([]));

    this.isFilterActive$ = combineLatest([
      this.searchService.select('filterSelection'),
      this.descendantsControl.valueChanges.pipe(
        startWith(this.descendantsControl.value)
      )
    ]).pipe(
      map(
        ([filterSelection, showAllDescendants]) =>
          showAllDescendants || !isEqual(filterSelection, this._defaultFilters)
      )
    );

    this.descendantsControl.valueChanges
      .pipe(startWith(this.descendantsControl.value), untilDestroyed(this))
      .subscribe(this.showAllDescendants);

    this.filter$ = this.filterService.select('filters');
    this.isLoadingResults$ = this.searchService.select('isLoadingResults');
    this.query$ = this.searchService.select('query');

    this.setQuery('');
  }

  ngOnDestroy() {}

  resetFilters(resetDescendantsControl = true) {
    if (resetDescendantsControl) {
      this.descendantsControl.reset(false);
    }
    this.searchService.update({
      description: 'Reset cards list filter',
      payload: {
        filterSelection: this._defaultFilters,
        sorting: this._defaultSorting
      }
    });
  }

  setQuery(query: string, resetDescendantsControl = false) {
    this.searchService.query = query ?? '';
    this.resetFilters(resetDescendantsControl);
  }

  trackByGuid(_: number, searchResult: ElasticSearchResult) {
    return searchResult.guid?.raw;
  }

  @Input()
  set defaultFilters(defaultFilters: FilterValuesByFieldName) {
    this._defaultFilters = defaultFilters;
    this.resetFilters(false);
  }

  @Input() set defaultSorting(defaultSorting: SortingDirectionByFieldName) {
    this._defaultSorting = defaultSorting;
    this.searchService.clearSortingSelection();
    this.searchService.applySorting(defaultSorting);
  }
}
