import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import {
  faSort,
  faSortAlphaDown,
  faSortAlphaUp
} from '@fortawesome/free-solid-svg-icons';
import { SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { untilDestroyed } from '@models4insight/utils';
import { SearchService } from '../../services/search/search.service';

@Component({
  selector: 'models4insight-sorting',
  templateUrl: './sorting.component.html',
  styleUrls: ['./sorting.component.scss']
})
export class SortingComponent implements OnInit, OnDestroy {
  readonly sortingIcon = faSort;
  readonly faSortUp = faSortAlphaUp;
  readonly faSortDown = faSortAlphaDown;

  sortBy: FormControl;
  sortDirection: FormControl;
  sortingForm: FormGroup;

  @Input() sortingOptions: string[] = [];

  constructor(private readonly searchService: SearchService) {}

  ngOnInit() {
    this.sortBy = new FormControl('_score');
    this.sortDirection = new FormControl(false);

    this.sortingForm = new FormGroup({
      sortBy: this.sortBy,
      sortDirection: this.sortDirection
    });

    this.sortingForm.valueChanges
      .pipe(untilDestroyed(this))
      .subscribe(({ sortBy, sortDirection }) =>
        this.searchService.applySorting({
          [sortBy]: sortDirection ? 'asc' : 'desc'
        })
      );

    this.searchService
      .select('sorting')
      .pipe(untilDestroyed(this))
      .subscribe(sorting => this.applySorting(sorting));
  }

  ngOnDestroy() {}

  toggleSortDirection() {
    this.sortDirection.setValue(!this.sortDirection.value);
  }

  private applySorting(sorting: SortingDirectionByFieldName) {
    const entries = Object.entries(sorting);

    if (entries.length === 0) return;

    const [sortBy, sortDirection] = entries[0];

    this.sortingForm.patchValue({
      sortBy,
      sortDirection: sortDirection === 'asc'
    });
  }
}
