import { Component, OnInit } from '@angular/core';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import { AtlasEntityDef } from '@models4insight/atlas/api';
import { combineLatest, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { iconsByType } from '../../../meta';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';
import { SuperTypeInformationService } from '../../../services/super-types/super-type-information.service';
import { TypeDefsService } from '../../../services/type-defs/type-defs.service';

@Component({
  selector: 'models4insight-type-super-type',
  templateUrl: './type-super-type.component.html',
  styleUrls: ['./type-super-type.component.scss']
})
export class TypeSuperTypeComponent implements OnInit {
  readonly iconsByType = iconsByType;
  readonly informationIcon = faInfoCircle;

  typeDef$: Observable<AtlasEntityDef>;
  superTypeDefs$: Observable<AtlasEntityDef[]>;
  superTypeNames$: Observable<string[]>;

  constructor(
    private readonly entityDetailsService: EntityDetailsService,
    private readonly superTypeService: SuperTypeInformationService,
    private readonly typeDefsService: TypeDefsService
  ) {}

  ngOnInit() {
    this.superTypeDefs$ = this.superTypeService.select('superTypeDefs');
    this.superTypeNames$ = this.superTypeService.select('superTypeNames');

    this.typeDef$ = combineLatest([
      this.entityDetailsService.select(['entityDetails', 'entity', 'typeName']),
      this.typeDefsService.select('entityTypesByName')
    ]).pipe(map(([typeName, typeDefs]) => typeDefs[typeName]));
  }
}
