import { Component, Inject, InjectionToken, OnInit, Optional } from '@angular/core';
import { EntityAttributes, Facet, FilterValuesByFieldName, SortingDirectionByFieldName } from '@models4insight/atlas/api';
import { defaultIfFalsy } from '@models4insight/utils';
import { Dictionary } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityDetailsService } from '../../../services/entity-details/entity-details.service';

export const SYSTEM_PROCESSES_FILTER_FACTORY = new InjectionToken<
  typeof createProcessesCardsFilter
>('SystemProcessesFilterFactory');

export const defaultFilters: FilterValuesByFieldName = {
  supertypenames: ['m4i_generic_process']
};

const defaultSorting: SortingDirectionByFieldName = {
  _score: 'desc'
};

const facets: Dictionary<Facet> = {
  typename: { type: 'value', size: 100 }
};

const sortingOptions: string[] = ['name'];

function createProcessesCardsFilter(relationshipAttributes: EntityAttributes) {
  const processes = relationshipAttributes.process ?? [];
  return { ...defaultFilters, guid: processes.map(process => process.guid) };
}

@Component({
  selector: 'models4insight-processes-cards',
  templateUrl: 'processes-cards.component.html',
  styleUrls: ['processes-cards.component.scss'],
  providers: [{ provide: 'searchServiceFacets', useValue: facets }]
})
export class ProcessesCardsComponent implements OnInit {
  readonly defaultSorting = defaultSorting;
  readonly sortingOptions = sortingOptions;

  defaultFilters$: Observable<FilterValuesByFieldName>;

  constructor(
    private readonly entityDetailsService: EntityDetailsService,
    @Optional()
    @Inject(SYSTEM_PROCESSES_FILTER_FACTORY)
    private readonly processesFilterFactory: typeof createProcessesCardsFilter
  ) {
    if (!this.processesFilterFactory) {
      this.processesFilterFactory = createProcessesCardsFilter;
    }
  }

  ngOnInit() {
    this.defaultFilters$ = this.entityDetailsService
      .select(['entityDetails', 'entity', 'relationshipAttributes'], {
        includeFalsy: true
      })
      .pipe(
        defaultIfFalsy([]),
        map(systems => this.processesFilterFactory(systems))
      );
  }
}
